<template>
  <div :class="['show-root', comOptions.showComBackGround? 'background' : '' ]">
    <div class="show-box">
        <img class="preview" @click="dialogShow = true" :src="comOptions ? comOptions.preview : ''" alt="icon" />
    </div>
    <component
      :is="id"
      @confirm="dialogShow = false"
      @close="dialogShow = false"
      :dialogShow="dialogShow"
      :options="comOptions"
    ></component>
  </div>
</template>
<script>
export default {
  props: {
    // componentName
    id: {
      type: String,
      default: () => "defaultDialog",
    },
    comOptions: {
      type: Object,
      default: () => null,
    },
  },
  components: {
    defaultDialog:  () => import("./default.vue"),
    closeDialog:    () => import("./close.vue"),
    contentDialog:  () => import("./content.vue"),
    iconDialog:     () => import("./icon.vue"),
    layerWhiteDialog1:() => import("./layer-white1.vue"),
    layerDarkDialog1:() => import("./layer-dark1.vue"),
    layerWhiteDialog2:() => import("./layer-white2.vue"),
    layerDarkDialog2:() => import("./layer-dark2.vue"),
  },
  data() {
    return {
      dialogShow: false,
    };
  },
  methods: {},
};
</script>
<style scoped lang="scss">
.show-root {
    padding: 15px 20px;
    background-color: rgba(#202020, 0.8);
}
.show-root.background {
    background-color: #efefef;
}
.show-box {
    display: flex;
    justify-content: center;
}
</style>
